//export page query
export const query = graphql`
query BlogPage {
  allMarkdownRemark(
    filter: {fileAbsolutePath: {glob: "**/src/blog/*.md"}}
  ) {
    nodes {
      frontmatter {
        slug
        stack
        title
      }
      id
    }
  }
}
`
